.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}

.nav-items > ul {
    list-style-type: none;
    display: flex;
}

.nav-items > ul > li{
    padding: 10px;
}

.logo{
    width: 80px;
}

.restaurant{
    width: 200px;
    border: 1px solid black;
    padding: 10px;
    margin: 45px;
}

.restaurant >img{
    width: 100%;
    height: 200px;
}

.restaurant-list{
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card{
    width: 200px;
    padding: 10px;
    margin: 45px;
    height: 200px;
    background-color: lightgray;
}

.menu{
    display: flex;
}